import request from '@/utils/request';
export function getPowerApplicationList(params) {
  return request({
    url: '/api/admin/power/application',
    method: 'get',
    params: params
  });
}
export function confirmPowerApplication(id, params) {
  return request({
    url: "/api/admin/power/application/".concat(id),
    method: 'put',
    params: params
  });
}
export function getPowerApplicationInProgressCount() {
  return request({
    url: '/api/admin/power/application/progress/count',
    method: 'get'
  });
}