export default {
  props: {
    /**
     * 审批面板的显示
     */
    visible: {
      type: Boolean,
      default: false
    },

    /**
     * 审批面板的标题
     */
    title: {
      type: String,
      default: '审批面板'
    },

    /**
     * 是否是审批的状态
     */
    isApprovel: {
      type: Boolean,
      default: true
    },

    /**
     * 是否需要显示权限申请审批的 modal
     */
    isPowerModal: {
      type: Boolean,
      default: true
    }
  },
  data: function data() {
    return {
      remarks: [{
        id: 1,
        remark: '申请合理，审批通过。',
        status: true
      }, {
        id: 2,
        remark: '申请不合理，审批不通过',
        status: false
      }],
      inputLabelText: '输入框',
      inputValue: '',
      submitLoading: false
    };
  },
  watch: {
    isApprovel: {
      handler: function handler(val) {
        if (val) {
          this.inputLabelText = '通过备注';
        } else {
          this.inputLabelText = '不通过备注';
        }
      },
      immediate: true
    }
  },
  beforeCreate: function beforeCreate() {
    this.form = this.$form.createForm(this, {
      name: 'remarkFormModal'
    });
  },
  methods: {
    handleTagClick: function handleTagClick(tagContent) {
      this.inputValue = tagContent;
    },
    showModal: function showModal() {
      this.visible = true;
    },
    handleOk: function handleOk(e) {
      var _this = this;

      var form = this.$refs.collectionForm.form;
      form.validateFields(function (err, values) {
        console.log(values);

        if (err) {
          return;
        }

        if (_this.isPowerModal) {
          _this.$emit('submit', values);
        } else {
          _this.$emit('submit');
        }

        _this.resetFormFields();
      });
    },
    startLoading: function startLoading() {
      this.submitLoading = true;
    },
    stopLoading: function stopLoading() {
      this.submitLoading = false;
    },
    handleCancel: function handleCancel(e) {
      this.resetFormFields();
      this.$emit('cancel', e);
    },
    resetFormFields: function resetFormFields() {
      this.inputValue = '';
    }
  }
};